header {
	position: relative;
	height: 50px; }
nav {
	position: fixed;
	width: 100%;
	height: 50px;
	background-color: #212121;
	z-index: 2; }
.menu {
	display: flex;
	justify-content: space-between;
	padding: 13px 70px;
	margin: 0;
	list-style-type: none; }
.menu_link {
	color: #ffffff;
	font-size: 15px;
	font-weight: 300; }
.menu_item {
	border-right: 2px dotted black;
	padding-right: 28px; }
.subheader {
	position: absolute;
	top: 50px;
	width: 100%;
	height: 84px;
	background-color: rgba(0, 0, 0, 0.6);
	&_logo {
		color: cornflowerblue;
		font-weight: 700;
		font-size: 25px;
		margin-top: 10px; }
	&_off {
		margin-top: 6px;
		opacity: 0.5;
		color: #ffffff;
		font-size: 13px;
		font-weight: 400; }
	&_call {
		color: #ffffff;
		font-size: 12px;
		font-weight: 300;
		margin-top: 19px;
		text-align: right; }
	&_phone {
		display: block;
		margin-top: 8px;
		color: #ffffff;
		font-size: 22px;
		font-weight: 400;
		text-transform: uppercase;
		text-align: right;
		line-height: 12px; }
	&_btn {
		margin-top: 19px;
		width: 172px;
		height: 46px;
		box-shadow: inset 0 -3px 0 rgba(0, 0, 0, 0.12);
		border: none;
		border-radius: 4px;
		background-image: linear-gradient(to top, #1eacc7 0%, #4ce2ff 100%);
		text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.21);
		color: #ffffff;
		font-size: 14px;
		font-weight: 400;
		text-transform: uppercase;
		@include btn_hover; } }
.promo {
	min-height: 800px;
	background: url('../img/taxi_bg.jpeg') center (center / cover) no-repeat;
	padding: 150px 0 135px 0; }
.promo_header {
	color: #030024;
	font-weight: 700;
	text-transform: uppercase;
	font-size: 60px;
	line-height: 60px;
	margin: 0;
	text-align: center; }
.promo_subheader {
	color: #ffffff;
	font-weight: 700;
	text-transform: uppercase;
	font-size: 36px;
	line-height: 36px;
	margin: 0;
	margin-top: 22px;
	text-align: center; }
.uber {
	color: #000000; }
.bolt {
	color: rgb(62, 224, 62); }
.free {
	color: red; }
.now {
	color: rgb(103, 103, 248); }
.promo_descr {
	color: #ffffff;
	font-size: 18px;
	font-weight: 400;
	line-height: 24px;
	text-align: center;
	margin-top: 23px; }
.promo_btn {
	display: block;
	margin: 118px auto 0 auto;
	width: 259px;
	height: 63px;
	box-shadow: inset 0 -4px 0 rgba(0, 0, 0, 0.12);
	border: none;
	border-radius: 4px;
	background-image: linear-gradient(to top, #1eacc7 0%, #4ce2ff 100%);
	text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.21);
	color: #ffffff;
	font-size: 18px;
	font-weight: 400;
	text-transform: uppercase;
	@include btn_hover; }
.reason {
	padding: 71px 0 65px 0; }
.persp {
	margin: 0 auto;
	height: 24px;
	width: 112px;
	padding: 5px 12px;
	background-color: #000000;
	color: #ffffff;
	font-size: 11px;
	font-weight: 300;
	text-transform: uppercase;
	text-align: center; }

.title {
	margin-top: 38px;
	color: #222222;
	font-size: 38px;
	font-weight: 700;
	text-transform: uppercase;
	text-align: center;
	z-index: 2; }
.subtitle {
	color: #222222;
	font-size: 17px;
	font-weight: 400;
	text-align: center;
	z-index: 2; }
.reason .reason_block {
	display: flex;
	justify-content: center;
	align-items: center;
	margin-top: 35px; }
.reason_round {
	display: flex;
	justify-content: center;
	align-items: center;
	margin-right: 26px;
	width: 113px;
	height: 113px;
	background-color: #1eacc7;
	border-radius: 100%; }
.reason_descr {
	width: 343px; }
.reason_subtitle {
	line-height: 21px;
	color: #1eacc7;
	font-size: 17px;
	font-weight: 400; }
.reason_text {
	color: #202020;
	font-size: 15px;
	font-weight: 300;
	line-height: 21px; }
.mobile {
	min-height: 864px;
	padding: 48px 0 79px 0;
	background: url(../img/mobile_bg.png) center (center / cover) no-repeat;
	.row {
		margin-top: 32px; }
	&_item {
		position: relative;
		width: 320px;
		height: 280px;
		margin: 0 auto;
		margin-bottom: 19px;
		padding-top: 92px;
		&_1 {
			background: url(../img/cart_1.png) center (center / cover) no-repeat; }
		&_2 {
			background: url(../img/cart_2.png) center (center / cover) no-repeat; }
		&_3 {
			background: url(../img/cart_3.png) center (center / cover) no-repeat; }
		&_4 {
			background: url(../img/cart_4.png) center (center / cover) no-repeat; }
		&_5 {
			background: url(../img/cart_5.png) center (center / cover) no-repeat; }
		&_6 {
			background: url(../img/cart_6.png) center (center / cover) no-repeat; }
		&_subtitle {
			color: #ffffff;
			font-size: 24px;
			font-weight: 400;
			line-height: 31px;
			text-transform: uppercase;
			text-align: center; }
		&_plus {
			display: flex;
			justify-content: center;
			align-items: center;
			position: absolute;
			bottom: 35px;
			left: 50%;
			transform: translateX(-50%);
			width: 26px;
			height: 26px;
			background-color: #000000;
			margin: 0 auto;
			border-radius: 100%;
			padding: 6px;
			cursor: pointer; } } }

.car {
	position: relative;
	min-height: 895px;
	padding: 57px 0 138px 0;
	background: url('../img/part_car.png') center (center / cover) no-repeat;
	&_img {
		position: relative;
		display: block;
		margin: 56px auto 0 auto;
		z-index: 2; }
	&_descr {
		text-align: center;
		color: #222222;
		font-size: 17px;
		font-weight: 300;
		line-height: 24px; } }
.black {
	position: absolute;
	top: 0;
	left: 0;
	height: 499px;
	width: 100%;
	background-color: #070716;
	z-index: 1; }
.persp_1 {
	position: relative;
	margin: 0 auto;

	height: 24px;
	width: 112px;
	padding: 5px 12px;
	background-color: #ffffff;
	color: #000000;
	font-size: 11px;
	font-weight: 300;
	text-transform: uppercase;
	text-align: center;
	z-index: 2; }
.title_white {
	position: relative;
	color: #ffffff;
	margin-top: 38px;
	font-size: 38px;
	font-weight: 700;
	text-transform: uppercase;
	text-align: center;
	z-index: 2; }
.subtitle_white {
	position: relative;
	margin-top: 19px;
	font-size: 17px;
	color: white;
	font-weight: 400;
	text-align: center;
	z-index: 2; }

.require {
	padding: 68px 0 84px 0;
	&_block {
		margin: 33px 0;
		&_nmb {
			margin-bottom: 0px; } }
	&_title {
		color: #1ca;
		font-size: 22px;
		font-weight: 400; }
	&_list {
		list-style-type: none;
		padding-left: 50px;
		margin-top: 22px;
		li {
			position: relative;
			font-size: 17px;
			font-weight: 300;
			line-height: 24px;
			margin-bottom: 10px;
			&::before {
				content: '';
				display: block;
				position: absolute;
				left: -32px;
				top: 3px;
				width: 21px;
				height: 20px;
				background: url('../img/check_blue.png') center (center / cover) no-repeat; } } }
	&_descr {
		margin-top: 25px;
		font-size: 17px;
		font-weight: 300;
		line-height: 24px;
		color: #1ca; }
	.warning {
		.require_title {
			color: red; }
		.require_list {
			li {
				&::before {
					background: url('../img/check_red.svg') center (center / cover) no-repeat; } } } } }

.world {
	position: relative;
	min-height: 717px;
	padding: 49px 0;
	background: url(../img/world_bg.png) center (center / cover) no-repeat;
	&_link {
		position: absolute;
		left: 50%;
		transform: translateX(-50%);
		bottom: 49px;
		text-align: center;
		font-size: 15px;
		font-weight: 400;
		line-height: 68px;
		text-transform: uppercase;
		color: #1fbad6;
		&:hover {
			text-decoration: none;
			color: #fff; } } }

.footer {
	padding: 14px 23px 39px 23px;
	&_divider {
		width: 300px;
		height: 4px;
		margin: 0 auto;
		background-color: black; }
	&_links {
		width: 390px;
		margin: 0 auto;
		margin-top: 15px;
		&_main {
			display: flex;
			justify-content: space-between;
			padding: 0 10px;
			a {
				color: #070716;
				font-size: 15px;
				font-weight: 300;
				text-transform: uppercase; } }
		&_sub {
			display: flex;
			justify-content: space-between;
			margin-top: 10px;
			a {
				color: #999999;
				font-size: 11px;
				font-weight: 300;
				text-transform: uppercase; } }
		&_lang {
			display: block;
			margin: 0 auto;
			color: #1fbad6;
			font-size: 11px;
			font-weight: 400;
			margin-top: 19px;
			text-transform: uppercase;
			text-align: center; } } }
